import React, { useState } from "react";
import "./uploadFile.scss";

const UploadCustomFile: React.FC<{
  setBase64ValCB: (image64: string | ArrayBuffer | null) => void;
  setFileDataValCB?: (file: File) => void;
}> = ({ setBase64ValCB, setFileDataValCB }) => {
  const [imageUrl, setImageUrl] = useState<string>();
  const [base64Val, setBase64Val] = useState<string | ArrayBuffer | null>();
  const [fileData, setFileData] = useState(null);

  //HANDEL CHANGE INPUT

  const handleChange = (event: any) => {
    const file = event.target?.files?.[0];
    setFileData(file);

    if (file) {
      // Get base64 representation
      const reader = new FileReader();
      reader.addEventListener("load", () => {
        setBase64Val(reader.result);
        setBase64ValCB(reader.result!);
        setFileDataValCB && setFileDataValCB!(file);
      });
      reader.readAsDataURL(file);

      // Get URL representation
      const url = URL.createObjectURL(file);
      setImageUrl(url);
    }
  };

  return (
    <div>
      <label htmlFor="upload-button">
        {imageUrl ? (
          <img src={imageUrl} alt="dummy" className="img-preview" />
        ) : (
          <div className="upload-btn">
            <span className="fa-stack fa-2x mt-3 mb-2">
              <i className="fas fa-circle fa-stack-2x" />
              <i className="fas fa-store fa-stack-1x fa-inverse" />
            </span>
            <img
              src={require("../../assets/img/gallery-add.jpg")}
              alt=""
              title="Upload Image"
            />
          </div>
        )}
      </label>
      <input
        type="file"
        id="upload-button"
        style={{ display: "none" }}
        onChange={handleChange}
      />
    </div>
  );
};

export default UploadCustomFile;
