import { Dropdown, Menu, Space, message, Tag } from "antd";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { Innstance } from "../../../Axios";
import ChangeStatus from "../../../components/change-status/change-status";
import ConfirmDelete from "../../../components/confirm-delete/confirmDelete";
import CustomTable from "../../../components/customTable/customTable";
import FilterAndSearch from "../../../components/FilterAndSearch";
import TableSearch from "../../../components/table-search/tableSearch";
import { UsherOptions, USHER_ENUM } from "../../../models/enums/status.enum";
import { USHER, USHER_FILTER } from "../../../models/usher.model";
import { RootState } from "../../../store";
import { getEventRecord } from "../../../store/Event/action";
import { UsherListAction } from "../../../store/Ushers/action";

const LeadersList: React.FC = () => {
  const [openFilter, setOpenFilter] = useState(false);
  const [query, setQuery] = useState<USHER_FILTER>({
    page: 1,
    type: "team_leader",
  });
  const { ushersList, meta } = useSelector((state: RootState) => state.ushers);
  const [openDelete, setOpenDelete] = useState(false);
  const [usherRecord, setUsherRecord] = useState<USHER>();
  const [openStatus, setOpenStatus] = useState<boolean>(false);

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const itemsList = (record: any) => {
    return (
      <Menu>
        <Menu.Item
          onClick={() => {
            handelChangeStatus(record);
          }}
          key="0"
        >
          <div className="flex">
            <img
              src={require("../../../assets/img/repeate-music.jpg")}
              alt=""
              title=""
            />
            <span>Change Status</span>
          </div>
        </Menu.Item>

        <Menu.Item onClick={() => HandelDeleteItem(record)} key="1">
          <div className="flex">
            <img
              src={require("../../../assets/img/trash 2.jpg")}
              alt=""
              title=""
            />
            <span>Delete</span>
          </div>
        </Menu.Item>
      </Menu>
    );
  };

  const colums = [
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
      render: (text: string, record: USHER) => (
        <Link to={`view/${record?.id}`} className="flex">
          <img src={record?.image ?? require("../../../assets/img/user.jpg")} />
          <span style={{ color: "#252525", fontWeight: "bold" }}> {text}</span>
        </Link>
      ),
    },
    {
      title: "Usher Id",
      dataIndex: "id",
      key: "id",
    },
    {
      title: "Total transferred amount",
      dataIndex: "total_transferred_amount",
      key: "total_transferred_amount",
    },
    {
      title: "Total events applied",
      dataIndex: "total_events_applied",
      key: "total_events_applied",
    },
    {
      title: "Current event",
      dataIndex: "current_event",
      key: "current_event",
    },
    {
      title: "Location",
      dataIndex: "location",
      key: "location",
    },

    {
      title: "Rating",
      dataIndex: "rating",
      key: "rating",
    },
    {
      title: "Applied Ushers",
      dataIndex: "applied_ushers",
      key: "applied_ushers",
    },

    {
      title: "Status",
      dataIndex: "status",
      key: "status",
      render: (text: string, record: USHER) => (
        <Tag
          style={{ padding: "5px 8px" }}
          color={
            record.status === USHER_ENUM.approved
              ? "#4C6E5C1A"
              : record.status === USHER_ENUM.pending
              ? "#4C6E5C1A"
              : ""
          }
        >
          <span
            style={{
              color:
                record.status === USHER_ENUM.approved
                  ? "green"
                  : record.status === USHER_ENUM.pending
                  ? "#4C6E5C"
                  : "",
            }}
          >
            {record.status}
          </span>
        </Tag>
      ),
    },
    {
      title: "",
      dataIndex: "actions",
      key: "actions",
      render: (_: any, record: any) => (
        <>
          <Dropdown
            dropdownRender={() => itemsList(record)}
            trigger={["click"]}
          >
            <button
              className="anchor-btn ptr"
              onClick={(e) => e.preventDefault()}
            >
              <Space>
                <img
                  src={require("../../../assets/img/Icon.jpg")}
                  alt=""
                  title=""
                  style={{
                    backgroundColor: "#fff",
                    padding: "5px",
                    border: "1px solid #ccc",
                    borderRadius: "50%",
                  }}
                />
              </Space>
            </button>
          </Dropdown>
        </>
      ),
    },
  ];

  const onClickFilter = () => {
    setOpenFilter(!openFilter);
  };

  const onClickSearchBtn = (name: any) => {
    setQuery(() => ({
      ...query,
      name: name.input.defaultValue,
    }));
  };

  const HandelClickFilter = () => {};

  const onClickNew = (): void => {
    navigate("create");
  };

  const onPageChangeFnc = () => {};

  const onReset = () => {
    setQuery({ page: 1, pageSize: 20, type: "team_leader" });
  };

  const getUsherList = () => {
    Innstance.get("admin/ushers", {
      params: {
        ...query,
      },
    }).then((res) => {
      dispatch(UsherListAction(res.data.data, res.data.meta));
    });
  };

  const HandelDeleteItem = (record: USHER): void => {
    setUsherRecord(record);
    setOpenDelete(true);
  };

  const handelChangeStatus = (record: USHER): void => {
    setOpenStatus(true);
    setUsherRecord(record);
  };

  const onSubmitdelete = () => {
    Innstance.delete(`admin/ushers/${usherRecord?.id}/destroy`)
      .then((res) => {
        message.success("deleted succssfully !");
        setQuery((oldQuery) => ({ page: 1, ...oldQuery }));
      })
      .catch((error) => {
        console.log(error);
      });
    setOpenDelete(false);
  };

  const onCancelDelete = () => {
    setOpenDelete(false);
  };

  const onChangeStatusSubmit = (status: USHER_ENUM): void => {
    Innstance.patch(`/admin/ushers/${usherRecord?.id}/change-status`, {
      status: status,
    })
      .then((response) => {
        message.success("Changed succssfully !");
        setQuery((oldQuery) => ({ page: 1, ...oldQuery }));
        setOpenStatus(false);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    getUsherList();
  }, [query]);

  return (
    <div className="usherList">
      <TableSearch
        addType="Leader"
        onClickFilter={onClickFilter}
        clickNew={onClickNew}
        onClickSearchBtn={onClickSearchBtn}
        showAddBtn={true}
        showFilterBtn={true}
      />
      {openFilter && (
        <FilterAndSearch
          onFilter={HandelClickFilter}
          setQuery={setQuery}
          query={query}
          onReset={onReset}
          filters={["name", "status"]}
        />
      )}
      <CustomTable
        colums={colums}
        data={ushersList}
        onPageChange={onPageChangeFnc}
        metaObj={meta}
      />

      {openDelete && (
        <ConfirmDelete
          openDelete={openDelete}
          submitDelete={onSubmitdelete}
          handelCancel={onCancelDelete}
          itemDelete={usherRecord}
        />
      )}

      {openStatus && (
        <ChangeStatus
          isOpen={openStatus}
          setIsOpen={setOpenStatus}
          handelSubmit={onChangeStatusSubmit}
          record={usherRecord}
          statusoption={UsherOptions}
        />
      )}
    </div>
  );
};

export default LeadersList;
