import {
  Button,
  Col,
  Form,
  Input,
  message,
  Radio,
  RadioChangeEvent,
  Row,
} from "antd";
import React, { useEffect, useState } from "react";
import { Innstance } from "../../../Axios";
import UploadCustomFile from "../../../components/upload-file/uploadFile";
import { LEADER } from "../../../models/leaders.mosel";
import { useNavigate, useParams } from "react-router-dom";

const LeaderForm = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const [imageProfile, setImageProfile] = useState<string | ArrayBuffer | null>(
    null
  );
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [leaderstatus, setLeaderstatus] = useState(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [form] = Form.useForm();

  const onSubmit = (values: LEADER) => {
    if (!leaderstatus) {
      message.warning("please choose status !");
      return;
    }

    if (imageProfile) {
      values.image = imageProfile;
    }

    setLoading(true);

    const formData = new FormData();
    formData.append("phone", "+966" + values?.phone);
    formData.append("status", leaderstatus!);
    formData.append("name", values?.name!);
    formData.append("email", values?.email!);
    if (imageFile) {
      formData.append("image", imageFile!);
    }

    if (id) {
      Innstance.put(`admin/leaders/${id}`, formData)
        .then((response) => {
          message.success("add succssfully !");
          navigate("/leaders");
          setLoading(false);
        })
        .catch((error) => {
          console.log(error);
          setLoading(false);
        });
    } else {
      Innstance.post("admin/leaders", formData)
        .then((response) => {
          message.success("add succssfully !");
          navigate("/leaders");
          setLoading(false);
        })
        .catch((error) => {
          console.log(error);
          setLoading(false);
        });
    }
  };

  useEffect(() => {
    if (id) {
      Innstance.get(`/admin/ushers/${id}`)
        .then((response) => {
          const leader = response?.data?.data;
          setLeaderstatus(leader?.status);
          form.setFieldsValue({
            name: leader?.name,
            phone: leader?.phone?.substring(4, leader?.phone?.length),
            status: leader?.status,
            email: leader?.email,
          });
        })
        .catch((error) => console.log(error));
    }
  }, [id]);

  const onSubmittedFailed = () => {
    console.log("Faild sbmitted !");
  };

  const onChangeStatus = ({ target: { value } }: RadioChangeEvent) => {
    setLeaderstatus(value);
  };

  const getBase64Val = (image64: string | ArrayBuffer | null): void => {
    setImageProfile(image64);
  };

  const getFileVal = (file: File): void => {
    setImageFile(file);
  };

  return (
    <div className="leader-form">
      <Form
        form={form}
        onFinish={onSubmit}
        onFinishFailed={onSubmittedFailed}
        labelCol={{ span: 24 }}
        layout="vertical"
      >
        <Row gutter={[40, 40]}>
          <Col lg={5}>
            <UploadCustomFile
              setBase64ValCB={getBase64Val}
              setFileDataValCB={getFileVal}
            />
          </Col>
          <Col lg={19}>
            <Row gutter={[20, 20]}>
              <Col lg={12}>
                <Form.Item<LEADER>
                  label="Name"
                  rules={[
                    { required: true, message: "This field is required" },
                  ]}
                  name="name"
                >
                  <Input
                    placeholder="Name"
                    size="large"
                    className="custom-input"
                    name="name"
                  />
                </Form.Item>
              </Col>

              <Col lg={12}>
                <Form.Item<LEADER>
                  name="email"
                  label="Email"
                  rules={[{ required: true, type: "email" }]}
                >
                  <Input type="email" size="large" />
                </Form.Item>
              </Col>

              <Col lg={12}>
                <Form.Item<LEADER>
                  label="Phone Number"
                  rules={[
                    { required: true, message: "This field is required" },
                  ]}
                  name="phone"
                >
                  <Input
                    addonBefore="+966"
                    placeholder="Phone Number"
                    size="large"
                    className="custom-input"
                    name="phone"
                  />
                </Form.Item>

                <Form.Item<LEADER>
                  label="Status"
                  rules={[
                    { required: true, message: "This field is required" },
                  ]}
                >
                  <Radio.Group onChange={onChangeStatus} value={leaderstatus}>
                    <Radio value="approved">Active</Radio>
                    <Radio value="inactive">Inactive</Radio>
                  </Radio.Group>
                </Form.Item>
              </Col>

              <Col lg={24}>
                <Button
                  type="primary"
                  htmlType="submit"
                  className={`w-100 mb-1-5 btn ${loading ? "spinner" : ""}`}
                  disabled={loading}
                >
                  Save
                </Button>
                <Button
                  type="default"
                  className="w-100 btn"
                  onClick={() => navigate("/leaders")}
                >
                  Cancel
                </Button>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
    </div>
  );
};

export default LeaderForm;
