import { Button, Col, Row } from "antd";
import React, { useEffect, useState } from "react";
import Tabs from "../../../components/tabs/tabs";
import { TABS } from "../../../models/tab";
import LeaderInfo from "./leaderInfo";
import LeaderActivities from "./leaderActivities";
import { useNavigate, useParams } from "react-router-dom";
import { Innstance } from "../../../Axios";
import { LEADER_ACTIVITIES, USHER } from "../../../models/usher.model";

const LeaderView = () => {
  const [tabMenu, setTabMenu] = useState<TABS[]>([
    { name: "Leader Info", width: "50", index: 0 },
    { name: "Leader's Activity", width: "50", index: 1 },
  ]);

  const { id } = useParams();
  const navigate = useNavigate();

  const [currentTab, setCurrentTab] = useState<number>(0);
  const [leaderDetails, setLeaderDetails] = useState<USHER>();
  const [leaderActivities, setLeaderActivites] = useState<LEADER_ACTIVITIES[]>(
    []
  );

  const onClickTab = (idx: number) => {
    setCurrentTab(idx);
  };

  const returTabComponent = (type: number): React.ReactElement => {
    switch (type) {
      case 0:
        return <LeaderInfo leaderDetails={leaderDetails!} />;
      case 1:
        return <LeaderActivities leaderActivitiesData={leaderActivities} />;

      default:
        return <LeaderInfo leaderDetails={leaderDetails!} />;
    }
  };

  useEffect(() => {
    if (id) {
      getLeaderInfo(+id);
      getLeaderActivities(+id);
    }
  }, []);

  const getLeaderInfo = (leaderId: number): void => {
    Innstance.get("/admin/ushers/" + leaderId)
      .then((response) => {
        setLeaderDetails(response?.data?.data);
      })
      .catch((error) => console.log(error));
  };

  const getLeaderActivities = (leaderId: number): void => {
    Innstance.get(`admin/ushers/${leaderId}/activities`)
      .then((response) => {
        setLeaderActivites(response?.data?.data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const onEditProfile = () => {
    navigate(`/leaders/edit/${id}`);
  };

  return (
    <div className="leader-view">
      <Row>
        <Col lg={24}>
          <Row>
            <Col lg={16}>
              <Tabs tabs={tabMenu} tabClick={onClickTab} />
            </Col>
            <Col lg={8} className="flex-end">
              <Button type="default" size="large" onClick={onEditProfile}>
                Edit Profile
              </Button>
            </Col>
          </Row>
        </Col>

        <Col lg={24}>{returTabComponent(currentTab)}</Col>
      </Row>
    </div>
  );
};

export default LeaderView;
