import { Dropdown, Menu, Space, Tag } from "antd";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import ConfirmDelete from "../../../components/confirm-delete/confirmDelete";
import CustomTable from "../../../components/customTable/customTable";
import TableSearch from "../../../components/table-search/tableSearch";
import { USHER_ENUM } from "../../../models/enums/status.enum";
import {
  LEADER_ACTIVITIES,
  USHER,
  USHER_FILTER,
} from "../../../models/usher.model";

const LeaderActivities: React.FC<{
  leaderActivitiesData: LEADER_ACTIVITIES[];
}> = ({ leaderActivitiesData }) => {
  const [openFilter, setOpenFilter] = useState(false);
  const [query, setQuery] = useState<USHER_FILTER>({
    page: 1,
    type: "team_leader",
  });
  const [openDelete, setOpenDelete] = useState(false);
  const [usherDelete, setUsherDelete] = useState<USHER>();

  const colums = [
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
      render: (text: string, record: USHER) => (
        <Link to={`${record?.id}`} className="flex">
          <img src={record?.image ?? require("../../../assets/img/user.jpg")} />
          <span style={{ color: "#252525", fontWeight: "bold" }}> {text}</span>
        </Link>
      ),
    },
    {
      title: "Usher Id",
      dataIndex: "id",
      key: "id",
    },
    {
      title: "Event Status",
      dataIndex: "event_status",
      key: "event_status",
    },
    {
      title: "Start Date",
      dataIndex: "start_date",
      key: "start_date",
    },
    {
      title: "Duration",
      dataIndex: "duration",
      key: "duration",
    },
    {
      title: "Location",
      dataIndex: "location",
      key: "location",
    },

    {
      title: "Total Questions",
      dataIndex: "total_questions",
      key: "total_questions",
    },

    {
      title: "Applicant Status",
      dataIndex: "applicant_status",
      key: "applicant_status",
      render: (text: string, record: USHER) => (
        <Tag
          style={{ padding: "5px 8px" }}
          color={
            record.applicant_status === USHER_ENUM.approved
              ? "#4C6E5C1A"
              : record.applicant_status === USHER_ENUM.pending
              ? "#4C6E5C1A"
              : ""
          }
        >
          <span
            style={{
              color:
                record.applicant_status === USHER_ENUM.approved
                  ? "green"
                  : record.applicant_status === USHER_ENUM.pending
                  ? "#4C6E5C"
                  : "",
            }}
          >
            {record.applicant_status}
          </span>
        </Tag>
      ),
    },
  ];

  const onClickFilter = () => {
    setOpenFilter(!openFilter);
  };

  const onClickSearchBtn = (name: any) => {
    console.log(name.input.defaultValue);
  };

  const HandelClickFilter = () => {};

  const onClickNew = (): void => {};

  const onPageChangeFnc = () => {};

  const HandelDeleteItem = (record: USHER): void => {
    setUsherDelete(record);
    setOpenDelete(true);
  };

  const onSubmitdelete = () => {
    setOpenDelete(false);
  };

  const onCancelDelete = () => {
    setOpenDelete(false);
  };

  useEffect(() => {}, [query]);

  return (
    <div className="usherList">
      <TableSearch
        addType="Leader"
        onClickFilter={onClickFilter}
        clickNew={onClickNew}
        onClickSearchBtn={onClickSearchBtn}
        showAddBtn={false}
        showFilterBtn={false}
      />

      <CustomTable
        colums={colums}
        data={leaderActivitiesData}
        onPageChange={onPageChangeFnc}
        metaObj={{}}
      />

      {openDelete && (
        <ConfirmDelete
          openDelete={openDelete}
          submitDelete={onSubmitdelete}
          handelCancel={onCancelDelete}
          itemDelete={usherDelete}
        />
      )}
    </div>
  );
};

export default LeaderActivities;
